.dock {
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  bottom: 0;
  min-height: 30px;
  -ms-flex-direction: column;
      flex-direction: column;
}

.dock-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  position: absolute;
  bottom: -300px;
  -webkit-transition: all .3s ease-in;
  -o-transition: all .3s ease-in;
  transition: all .3s ease-in;
  opacity: 0;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

.dock-container.show {
  bottom: 0;
  opacity: 1;
}

.dock-mask{
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: 0;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  bottom: -100px;
}

.dock-mask-inner {
  padding: 5px 15px;
  background: rgba(0,0,0,.5);
  border-radius: 3px 3px 0 0;
}

.dock-mask.show {
  opacity: 1;
  bottom: 0;
}

.intro {
  position: absolute;
  width: 50%;
  margin: 25px auto;
  padding: 25px;
  border-radius: 5px;
  background: rgba(0,0,0,.5);
  color: white;
  bottom: 30px;
  right: 30px;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

.email {
  color: white;
}

.smallDuang {
  height: 20px;
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
  border: none;
	margin: -4px;
}

@media screen and (max-width: 992px) {
  .intro {
    font-size: 1.5vw;
    bottom: 3vw;
    right: 3vw;
    position: relative;
  }
  .smallDuang {
    height: 2.5vw;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
    width: 2.5vw;
  }
  .container {
    display: none;
  }

  .dock-container {
    max-width: 100%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .dock-container > .button {
    font-size: 2vw;
    padding: 1vw;
    margin: 0;
    margin-bottom: 3vw;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
  }
}




