.cell {
  width: 100%;
  opacity: 0;
  background-size: cover;
  background-position: center;
  background: cover;
  overflow: hidden;
  background-repeat: no-repeat;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: enter;
          animation-name: enter;
  -webkit-transition:max-height 1s;
  -o-transition:max-height 1s;
  transition:max-height 1s;
  height: 100vh;
}

.cell-inner {
  width: auto;
  height: 100%;
  background: rgba(0,0,0,.6);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -webkit-box-shadow: inset 0 0 100px black;
          box-shadow: inset 0 0 100px black;
}

.cell-innerer {
  padding: 0px 75px 38px 75px;
  text-align: left;
  width: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  -webkit-transition:max-height 1s;
  -o-transition:max-height 1s;
  transition:max-height 1s;
  height: 100%;
  font-size: 1.1vw;
}

.backdrop {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.cell.Trailer {
  max-height: 945px;
}

.trailer {
  -ms-flex-positive: 1;
      flex-grow: 1; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

@-webkit-keyframes enter {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes enter {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@-webkit-keyframes tab-enter {
  0% {
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
    opacity: 0;
  }

  80% {
    opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes tab-enter {
  0% {
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
    opacity: 0;
  }

  80% {
    opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

.details {
  width: 30%;
  height: 358px;
  position: relative;
  margin-left: 5px;
}

.cell-top {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  position: relative;
  height: 45%;
  overflow: hidden;
}

.cell-bottom {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 45%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  position: relative;
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow: hidden;
}
.cell-left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex: 1 1 0pt;
      flex: 1 1 0pt;
}
.cell-middle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex: 1 1 0pt;
      flex: 1 1 0pt;
  -ms-flex-pack: center;
      justify-content: center;
}
.cell-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 1 1 0pt;
      flex: 1 1 0pt;
  overflow: hidden;
  max-height: 450px;
}

img {
  max-height: 450px;
  border-radius: 9px;
	max-width: 300px;
	-o-object-fit: cover;
	   object-fit: cover;
}

.title {
  font-size: 3vw;
  font-weight: 900;
  text-align: center;
  background: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  outline: none !important;
  height: 10%;
  margin: 0;
  padding: 0;
  padding-top: 1%;
}

.score {
  text-align: center;
  border-radius: 50px;
  margin-top: 20px;
  height: 200px;
  width: 200px;
  left: 35px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 9vw;
}

.ten {
  font-size: 2vw;
}

.must {
  position: absolute;
  max-height: 200px;
  width: 20vw;
  z-index: 998;
  border: none;
}

.pull-forward {
  z-index: 999;
}

p {
	margin: 0;
}

.description-heading {
	margin: 8px;
	font-weight: 800;
}

a {
	margin:auto;
	width: 100px;
	text-align: center;
	text-decoration: none;
	color: white;
	font-weight: 700;
	font-size: 18px;
	padding: 10px;
	border-radius: 5px;
}

.tab-selector {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-line-pack: distribute;
      align-content: space-around;
}

iframe {
	margin: auto;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-name: tab-enter;
          animation-name: tab-enter;
}

.synopsis, .review {
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
  -webkit-animation-name: tab-enter;
          animation-name: tab-enter;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

.tab-content {
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
}

.omdb {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: absolute;
  bottom: 0;
  font-size: 12px;
}

.loadScore {
  font-size: 12px;
  padding: 0;
  background: none;
  cursor: pointer;
  text-decoration: underline;
}

@media screen and (max-width: 992px) {
  .cell {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    border-radius: 0;
    max-height: 2000px;
  }

  .cell.Trailer {
    max-height: 2245px;
  }

  iframe {
    width: 100vw;
  }

  .cell-top {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 60%;
  }

  .title {
    height: 5%;
  }

  .cell-bottom {
    height: 30%;
  }

  .cell-left {
    min-width: 50vw;
  }
  .cell-middle {
    min-width: 10vw;
  }
  .cell-right {
    min-width: 50vw;
  }

  .details {
    padding-left: 0;
    padding-right: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 0;
  }

  img {
    width: 30vw;
  }

  .score {
    margin: auto;
    left: 0;
    position: relative;
    height: auto;
  }

  .cell-innerer{
    padding: 16px 16px 0 16px;
    font-size: 2vw;
  }

  .trailer {
    display: none;
  }

}



